:root {
  --primary-150: hsl(193, 38%, 86%);
  --primary-300: hsl(150, 100%, 66%);
  --neutral-650: hsl(217, 19%, 38%);
  --neutral-750: hsl(217, 19%, 24%);
  --neutral-850: hsl(218, 23%, 16%);
  --family-manrope: "Manrope", sans-serif;
}

/*
  1. Use a more-intuitive box-sizing model.
*/
*, *::before, *::after {
  box-sizing: border-box;
}

/*
  2. Remove default margin and padding
*/
* {
  margin: 0;
  padding: 0;
}

/*
  Typographic tweaks!
  3. Add accessible line-height
  4. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

/*
  5. Improve media defaults
*/
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

/*
  6. Remove built-in form typography styles
*/
input, button, textarea, select {
  font: inherit;
}

/*
  7. Avoid text overflows
*/
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

/*
  8. Create a root stacking context
*/
#root, #__next {
  isolation: isolate;
}

body {
  display: grid;
  place-content: center;
  min-height: 100vh;
  width: 100%;
  background: var(--neutral-850);
  font-family: var(--family-manrope);
  font-weight: 500;
}

#container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 19rem;
  max-width: 100%;
  gap: 1rem;
  padding: 1rem;
  background: var(--neutral-750);
  border-radius: 0.5rem;
  box-shadow: 0.5rem 0.5rem 1rem rgba(0, 0, 0, 0.1333333333);
  position: relative;
}
@media (min-width: 48rem) {
  #container {
    gap: 2rem;
    width: 31.25rem;
  }
}

.advice-block {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: var(--primary-300);
}
.advice-block .quote-hashtag,
.advice-block .advice-number {
  font-size: 0.8rem;
}

.quote {
  width: 18.75rem;
  text-align: center;
  font-size: 1.75rem;
  color: var(--primary-150);
}

#dice {
  background-color: var(--primary-300);
  outline: none;
  appearance: none;
  border: none;
  padding: 1rem;
  border-radius: 100%;
  margin-top: 1rem;
  margin-bottom: -40px;
  cursor: pointer;
}

.hidden {
  display: none;
}
@media (min-width: 48rem) {
  .hidden {
    display: block;
  }
}

@media (min-width: 48rem) {
  .hidden-later {
    display: none;
  }
}